import React, {useState} from 'react';
import classes from './Paginator.module.scss';

let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 8}) => {
    let pagesCount = Math.ceil (totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionNumber = portionNumber * portionSize;

    return <div className={classes['container']}>
        {portionNumber > 1 &&
        <button className={classes['btn']}
                onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button>
        }

        {pages
            .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
            .map(p => {
            return <span className={`${classes['select-page']} ${currentPage === p && classes['selected']}`}
                         key={p}
                         onClick={(e) => { onPageChanged(p) }}>{p}</span>
            })
        }
        {portionCount > portionNumber &&
        <button className={classes['btn']}
                onClick={() => { setPortionNumber(portionNumber + 1)}}>NEXT</button>
        }
    </div>
}

export default Paginator;